@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary: #6948ff;
  --secondary: #553bcb;
  --darkgrey: #22262a;
  --lightgrey: #d3d3d3;
  --grey: #808080;
  --white: #f8f8f8;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #f8f8f8;
  color: var(--white);
}

body {
  background-color: #22262a;
  background-color: var(--darkgrey);
  color: #d3d3d3;
  color: var(--lightgrey);
  font-family: 'Poppins', sans-serif;
  line-height: 1.5;
}

.primary-color {
  color: #6948ff;
  color: var(--primary);
}
.navbar {
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background-color: var(--darkgrey);
}

.navbar img {
    width: 80px;
}

.nav-menu {
    display: flex;
}

.nav-item {
    padding: 1rem;
}

.hamburger {
    display: none;
    cursor: pointer;
}

@media screen and (max-width: 750px) {
    .nav-menu {
        position: fixed;
        left: -100%;
        top: 100px;
        flex-direction: column;
        justify-content: center;
        background-color: var(--darkgrey);
        width:100%;
        height: 100%;
        text-align: center;
        transition: 0.4s;
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-item {
        margin: 1.5rem;
        font-size: 1.8rem;
        justify-content: center;
    }

    .hamburger {
        display: flex;
    }
}
.project-card {
    border: 1px solid var(--lightgrey);
    border-radius: 20px;
    margin: 10px; 
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

}

.name {
    font-size: 20px;
    color: var(--lightgrey);
    font-weight: 700;
}

.desc {
    font-size: 14px;
    color: var(--lightgrey)
}

img {
    height: 200px;
    width: 200px;
    border-radius: 10px;
    margin: 0 auto;
}

.p-link {
    text-decoration: underline;
}

.guest {
    color: rgb(0, 233, 124);
    font-size: .8rem;
}

.guestD {
    font-size: .8rem;
    color: var(--lightgrey);
    padding: 0 3px;
}

.guestP {
    font-size: .8rem;
    color: var(--lightgrey);
    padding: 0 3px;
}

.login-container {
    display: flex;
    flex-direction: column;
    text-align: left;
    margin-left: 10px;
    font-size: .8em;
    line-height: 1.3em;
}
.hero {
    width: 100%;
    background: var(--darkgrey);
}


.project-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    color: rgb(191, 53, 53);
    height: 100vh;
    width: 75vw;
    margin: 0 auto;
    align-items: center;
    text-align: center;
    justify-content: center;
}




@media screen and (max-width:750px) {
    .hero {
        height: auto;
        width: 100%;
    }

    .project-container {
        grid-template-columns: 1fr;
    }
}





.about-container {       
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    align-items: center;
    margin: 0 50px;
    grid-template-rows: 280px 100px;
    margin-top: 30px;
}

.about {
    grid-row: span 2;
    
    margin: 0 auto;
    align-self: start;
}

.img {
    margin: 20px auto;
    align-self: start;
    border-radius: 50px;
}

.img-me {
    border-radius: 50%;
}

.logo-container {
    margin: 50px auto;
    text-align: center;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10px;
    gap: 10px;
}

h1 {
    text-align: center;
}

p {
    margin-top: 20px;
}

h1 {
    margin-bottom: 10px;
}

.github {
    color: #ccc;
}

.react {
    color: #61DBFB;
}

.node {
    color: #54ed84;
}

.js {
    color: #FFFF00;
}

.css {
    color: #FFA500;
}

.html {
    color: #ff0000;
}

.mongo {
    color: #4DB33D;
}

.express {
    color: #68A063;
}

.contact-container {
    background: var(--darkgrey);
    height: 100vh;
    display: grid;
    grid-template-columns: 2fr 1fr;
}
h2 {
    text-align: center;
    margin-bottom: 15px;
}
.form {
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: 0 auto;
}

input {
    border-radius: 5px;
    height: 30px;
    margin: 5px 0;
}

textarea {
    margin: 5px 0;
    height: 80px;
    resize: none;
}

.icon-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
}

a {
    text-align: center;
}

.icons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: 40vh;
}

.github {
    color: #fff;
    
}

.linkedin {
    color: #0A66C2;
    margin-left: 20px;
}

input[type='submit'] {
    background-color: rgb(70, 177, 70);
    width: 30%;
    cursor: pointer;
    margin-top: 15px;
    color: var(--white);
    border: none;
}


